import fs from "node:fs";
import path from "node:path";
import pkgDir from "pkg-dir";

let CONTENT_ENGINE_VERSION: string;

export function getGatsbyVersion(): string {
  if (!CONTENT_ENGINE_VERSION) {
    const packageJSON = JSON.parse(
      fs.readFileSync(
        path.join(pkgDir.sync(__dirname)!, "package.json"),
        "utf8",
      ),
    );

    CONTENT_ENGINE_VERSION = packageJSON.version;
  }

  return CONTENT_ENGINE_VERSION;
}
